import { ItemStyle } from './style';
import React, { Fragment } from 'react';
import { ArrowLeft, ArrowDown } from 'oah-ui/svg';

const Item = ({ item, toggleSidebar, toggleSubMenu, selectItem, id, Link }) => {
  const isActive = ({ location, href }) => {
    console.log(location, href);
    (location.pathname === item.link ||
      location.pathname === item.link + '/' ||
      location.pathname + '/' === item.link) &&
      !item.selected &&
      selectItem(id, false);
  };

  const handleToggleSubMenu = () => {
    toggleSubMenu(item);
  };

  return (
    <ItemStyle className={item.group ? 'menu-item menu-group' : 'menu-item'}>
      {item.group ? (
        <span>
          {item.icon && <i className={'menu-icon ' + item.icon} />}
          {item.title}
        </span>
      ) : item.link && !item.children ? (
        <Link
          to={item.link}
          getProps={isActive}
          title={item.title}
          target={item.target}
          className={item.selected ? 'active' : ''}
          onClick={toggleSidebar}
        >
          <i className={'menu-icon ' + item.icon} />
          <span className="menu-title">{item.title}</span>
        </Link>
      ) : item.url && !item.children ? (
        <a href={item.url} target={item.target} title={item.title}>
          <i className={'menu-icon ' + item.icon} />
          <span className="menu-title">{item.title}</span>
        </a>
      ) : item.children ? (
        <Fragment>
          <a
            href={item.link}
            title={item.title}
            onClick={e => {
              e.preventDefault();
              handleToggleSubMenu();
            }}
            className={item.selected ? 'active' : ''}
          >
            <i className={'menu-icon ' + item.icon} />
            <span className="menu-title">{item.title}</span>
            <i className="chevron">
              {item.expanded ? <ArrowDown /> : <ArrowLeft />}
            </i>
          </a>
          <ul
            className={
              item.expanded ? 'menu-items expanded' : 'menu-items collapsed'
            }
          >
            {item.children.map((item2, index) => {
              return (
                !item.hidden && (
                  <Item
                    key={index}
                    item={item2}
                    id={id + ',' + index}
                    Link={Link}
                    selectItem={selectItem}
                    toggleSidebar={toggleSidebar}
                    toggleSubMenu={toggleSubMenu}
                  />
                )
              );
            })}
          </ul>
        </Fragment>
      ) : (
        ''
      )}
    </ItemStyle>
  );
};

export default Item;
